*
{
  margin:0;
  padding:0;
}

body
{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
form{
  background-color: lightskyblue;
  border: 2px solid blue;
  height: 300px;
  width: 300px;
  padding:10px;
  margin: 20px;
}
label
{
  display: block;
}
input{
  height: 25px;
  width: 75%;
}
button
{
  background-color: green;
  color:white;
  padding:10px 20px;
  border: 0;
  margin-top: 20px;
  margin-right: 10px;
}